
.resume-hd-outer{
  background-color:#12343b;
/*  max-height: 100vh;*/
  padding: 4vw 0vw;

}
.resume-hd-box-img{
/*  height: 46.9vw;*/
  overflow: hidden;
  padding: 2vw 4vw 2vw 2.5vw;

}
.resume-hd-box-img img{
  width: 100%;
  height: 100%;
  animation: rotateImage 5s linear infinite;
}
@keyframes rotateImage{
  0%{
/*    transform: rotateY(0deg);*/
  transform: scale(.9) rotate(0deg);
  }
  50%{
    transform:scale(1.1) rotateY(180deg);
  }
  100%{
    transform:scale(.9) rotateY(360deg);
  }
  
}
.resume-hd-box-img img:hover{
  animation-play-state: paused;
}
.resume-hd-box{
  padding: 5vw;
  text-align: left;
}
.resume-hd{
  margin: 0px;
  padding: 0px;
  font-size: 3vw;
  text-align: left;
  letter-spacing: 1px;
  color: #fecb88;
  font-weight: bold;
}
.resume-hd-box h2{
  margin: 4vw 0vw 1vw 0vw;
  padding: 0px;
  letter-spacing: 1px;
  font-size: 4vw;
  font-family: 'Poppins',sans-serif;
  text-align: left;
  color: #fecb88;
  font-weight: bold;
}
.resume-hd-box p{
text-align: left;
font-size: 1.6vw;
color: white;
font-family: sans-serif;
letter-spacing: .1vw;
padding-bottom: 1.5vw;
}

.resume-hd-box button{
 background-color: white;
  border: 1px solid #422800;
  border-radius: 2vw;
  font-size: 1.5vw;
  font-weight: 600;
  margin: 2vw 1vw;
  padding: 1vw 3vw;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  transition: .3s;
}
.resume-hd-box button:hover{
  box-shadow: #fecb88 2px 2px 0 0;
}






/*media query*********************************************************************/
/*extra small*/
@media screen and (max-width:576px){
  .resume-hd-outer{
    padding: 4vw 2vw 8vw 2vw;
  }
  .resume-hd-box-img{
    padding: 4vw 8vw;
  }
  .resume-hd{
    font-size: 9vw;
  }
  .resume-hd-box h2{
    font-size: 15vw;
    margin: 7vw 0vw 2vw 0vw;
  }
  .resume-hd-box p{
    font-size: 6.5vw;
  }
  .resume-hd-box button{
    font-size: 4.5vw;
    padding: 2vw 5vw;
  }
  .resume-hd-box button:hover{
  box-shadow: #fecb88 2px 2px 0 0;
  }
  .resume-hd-box-img{
    height: 103.9vw;
  }
  .template-hd-div{
    background-color: #12343b;
  }
  .template-hd-div h2{
    color: #fecb88;
  }
}


