    font-size: 14vw;
    margin: 7vw 0vw 2vw 0vw;
  }
  .resume-hd-box p{
    font-size: 5.5vw;
  }
  .resume-hd-box button{
    font-size: 4.5vw;
    padding: 2vw 5vw;
  }
  .resume-hd-box button:hover{
  box-shadow: #fecb88 2px 2px 0 0;
  }
  .resume-hd-box-img{
    height: 103.9vw;
  }
   .template-hd-div{
    background-color: #12343b;
  }
  .template-hd-div h2{
    color: #fecb88;
  }
}


